/* ===============================================
---------- Breadcrumb Css ----------
=================================================*/

.breadcrumb {
  position: relative;
  z-index: 1;
  padding: 155px 0 70px;
  background-color: #ddd;
  @include md-screen {
    padding: 115px 0 60px;
  }
  @include sm-screen {
    padding: 105px 0 40px;
  }
  &::before {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    background-color: hsl(var(--white) / 0.1);
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  &__wrapper {
    text-align: center;
  }
  &__title {
    margin-bottom: 10px;
    color: hsl(var(--black));
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__item {
    color: hsl(var(--black));
    padding: 0 5px;
    font-weight: 400;
    @include fs-16;
    &-text {
      color: hsl(var(--base));
    }
  }
  &__link {
    color: hsl(var(--black));
    font-weight: 500;
    &:hover {
      color: hsl(var(--base));
    }
  }
} 
