:root [data-theme=dark] {
  --white: 200 9% 7%;
  --black: 210 40% 98%;
}

/* Fully Fit image Css */
.fit-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Display Flex Css */
.flex-wrap, .form--radio, .form--check {
  display: flex;
  flex-wrap: wrap;
}

.flex-align, .action-buttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.flex-center {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.flex-between {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

/* Positioning Css Class */
.pa-extend, .alert__link::before {
  position: absolute;
