  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 0;
  height: 0;
  background-color: hsl(var(--base));
  filter: blur(9rem);
  transition: all 0.6s;
}

.choose-item .icon {
  background: hsl(var(--black)/0.2);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  border-radius: 10px;
  border: 2px solid hsl(var(--border-color)/0.1);
  margin-bottom: 35px;
}

.choose-item i {
  font-size: 30px;
  line-height: 1;
}

/* ===============================================
---------- Testimonial Section ----------
=================================================*/
.arrow-btns {
  display: flex;
  align-items: center;
  gap: 15px;
}

.arrow-btns button {
  background: hsl(var(--white)/0.1);
  width: 50px;
  height: 50px;
  border-radius: 5px;
  color: hsl(var(--white));
}

.testimonial-slide {
  border-radius: 10px;
  background: hsl(var(--base)/0.01);
  backdrop-filter: blur(10px);
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.testimonial-slide__item {
  position: relative;
}

.testimonial-slide__item::before {
  content: "\f10e";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 40px;
  opacity: 0.3;
}

.testimonial-slide__item p {
  border-bottom: 1px solid hsl(var(--white)/0.1);
  padding-bottom: 30px;
  margin-bottom: 30px;
}

.testimonial-slide__item .user {
  display: flex;
  align-items: center;
  gap: 15px;
}

.testimonial-slide__item .user img {
  width: 55px;
  height: 55px;
  border-radius: 100px;
  object-fit: cover;
}

.testimonial-slide__item .user h5 {
  margin-bottom: 0;
}

.testimonial-slide__item .user span {
  font-size: 0.875rem;
}

.text-inner {
  animation: circle_rated 15s linear infinite;
  position: relative;
  z-index: 22;
  fill: transparent;
  width: 220px;
  height: 220px;
  margin: 0px auto 0;
}

.text-inner svg {
  width: 220px;
  max-width: 100%;
  height: auto;
  overflow: visible;
  word-spacing: 10px;
  transform: rotate(0) scaleX(1) scaleY(1);
  position: relative;
}

.text-inner text {
  fill: hsl(var(--white));
  direction: ltr;
  transition: 0.3s stroke, 0.3s stroke-width, 0.3s fill;
  font-size: 24px;
  font-weight: 400;
  position: relative;
  z-index: 2;
  letter-spacing: 8px;
  text-transform: uppercase;
}

.circle-animation-img {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
  display: inline-block;
  line-height: 0;
}

.circle-animation-img i {
  animation: quit_ani 5s linear infinite;
  transition: 0.5s;
  font-size: 50px;
}

@keyframes quit_ani {
  0% {
    transform: rotate3d(0, 0, 0);
  }
  50% {
    transform: rotate3d(0, 1, 0, 180deg);
  }
  100% {
    transform: rotate3d(0, 1, 0, 359deg);
  }
}
@keyframes circle_rated {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* ===============================================
---------- Faq Section ----------
=================================================*/
/* ===============================================
---------- Blog Section ----------
=================================================*/
.blog-item {
  position: relative;
  z-index: 11;
  border-radius: 10px;
  overflow: hidden;
}

.blog-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, hsl(var(--base)/0.9) 0%, rgba(0, 0, 0, 0) 64%);
  transition: all 0.6s;
}

.blog-item:hover::before {
  background: linear-gradient(0deg, hsl(var(--base)/0.9) 0%, rgba(0, 0, 0, 0) 80%);
}

.blog-item:hover .blog-text {
  bottom: 0;
}

.blog-item:hover .read-blog .blog-line::before {
  width: 100%;
}

.blog-item .blog-img {
  position: relative;
}

.blog-item .blog-img .blog-date {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 6px 15px;
  font-size: 0.875rem;
}

.blog-item .blog-img .blog-date:before {
  opacity: 0.7;
  backdrop-filter: blur(10px);
}

.blog-item .blog-img img {
  transition: all 0.6s;
}

.blog-item .blog-text {
  position: absolute;
  bottom: -76px;
  width: 100%;
  z-index: 1;
  padding: 30px;
  transition: all 0.6s;
}

.blog-item .blog-text h4 {
  padding-bottom: 7px;
}

.blog-item .blog-text h4 a:hover {
  color: hsl(var(--white));
}

.blog-item .read-blog {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
}

.blog-item .read-blog .blog-line {
  width: 100%;
  transition: all 0.6s;
  position: relative;
  z-index: 1;
}

.blog-item .read-blog .blog-line::before {
  content: "";
  position: absolute;
  width: 0%;
  height: 1px;
  background: hsl(var(--white));
  transition: all 1.5s;
}

.blog-item .read-blog .arrow-btn {
  border: 2px solid hsl(var(--border-color)/0.7);
  min-width: 45px;
  height: 45px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: hsl(var(--white));
  transform: rotate(45deg);
  transition: all 0.4s;
}

.blog-item .read-blog .arrow-btn:hover {
  background: hsl(var(--black)/0.7);
  backdrop-filter: blur(5px);
}